@import url(https://fonts.googleapis.com/css?family=Numans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.login-btn {
  float: right;
  color: black;
  margin-left: 60px;
  padding-left: 50px;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #eee;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

nav {
  padding: 20px;
  display: flex;
  justify-content: center;
}
nav a {
  margin-left: 10%;
}
footer {
  margin-top: 360px;
  background-color: #eee;
  padding: 40px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.home-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 10px;
}
.foriegn {
  grid-column: 1;
}
.classics {
  grid-column: 2;
}
.casino {
  grid-column: 3;
}
.classics,
.foriegn,
.casino {
  margin-top: 40px;
}
.durak-link-image {
  background: url(../../static/media/durak.e89935ff.jpg);
}
.war-link-image {
  background: url(../../static/media/war.aa1d8dcc.jpg);
}
.poker-link-image {
  background: url(../../static/media/poker.06c674ba.jpg);
}
.war-link-image,
.durak-link-image,
.poker-link-image {
  margin-top: 40px;
  height: 200px;
  background-position: center;
  background-position-y: -1px;
  background-size: cover;
  background-repeat: no-repeat;
  border: 2px solid rgba(0, 0, 0, 0.8);
  border-radius: 12px;
}

.war-link-container a,
.durak-link-container a,
.casino-link-container a {
  color: black;
  padding-top: 12px;
  font-weight: 500;
}
@media (max-width: 450px) {
  .home-grid {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
  }

  .foriegn {
    grid-row: 1;
    grid-column: 2;
  }
  .classics {
    grid-row: 2;
    grid-column: 2;
  }
  .casino {
    grid-row: 3;
    grid-column: 2;
  }
}
@media (min-width: 700px) {
  .war-link-image,
  .durak-link-image,
  .poker-link-image {
    background-size: 20em;
    background-position-y: -40px;
  }
  .home-grid {
    margin-left: 50px;
  }
}
@media (min-width: 850px) {
  .war-link-image,
  .durak-link-image,
  .poker-link-image {
    background-size: 32em;
    background-position-y: -130px;
  }
  .poker-link-image {
    margin-right: 40px;
  }
}

#card {
  border: 2px solid black;
  border-radius: 8px;
  height: 450px;
  width: 300px;
  padding: 30px;
  margin: 0 auto;
  margin-top: 40px;
}
.card-suite-rank-container {
  text-decoration: none !important ;
  font-size: 42px;
  border: 3px solid #1ed71e;
  border-radius: 3px;
}
.card-rank,
.card-suite {
  font-size: 3em;
  float: left;
}
.card-image {
  border: 2px solid black;
  border-radius: 8px;
  height: 14em;
  width: 150px;
  padding: 30px;
  margin: 0 auto;
  margin-top: 25%;
}
#red {
  color: red;
}
#black {
  color: black !important;
}
.card-grid-container,
.score-grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.card1,
.player-score {
  grid-column: 1;
}
.card2,
.computer-score {
  grid-column: 2;
}
.get-card-btn {
  margin-top: 8%;
  margin-bottom: 10%;
}

.player-score-label,
.computer-score-label {
  background-color: #282c34;
  color: white;
  padding: 20px;
  font-size: 1.4em;
  font-weight: 500;
}
.player-score,
.computer-score {
  background-color: #515960;
  color: white;
  padding: 4px;
  padding: 10px;
  font-size: 1.2em;
  font-weight: 200;
}
@media (max-width: 450px) {
  .card-grid-container img {
    max-width: 200px;
  }
}

.back-of-card {
  position: absolute;
  margin-left: -40px;
  max-height: 209px;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  margin-top: 10%;
  border-radius: 20px;
}
.card1-container,
.card2-container {
  margin-left: -120px;

  padding: 20px;
}
.card-1,
.card-4 {
  margin-left: -200px;
}
.card-2,
.card-5 {
  margin-left: -160px;
}
.card-3,
.card-6 {
  margin-left: -120px;
}
.top-card {
  position: absolute;
  margin-left: 20px;
  z-index: 5;
}
@media (max-width: 450px) {
  .card-grid-container img {
    /* max-width: 200px; */
  }
  .card {
    /* width: 140px; */
  }
  .back-of-card {
    width: 290px;
    margin-top: 48px;
  }
  .war-card-match {
    margin-left: 50px;
    width: 210px;
  }
  .card-1,
  .card-4 {
    margin-left: -200px;
  }
  .card-2,
  .card-5 {
    margin-left: -160px;
  }
  .card-3,
  .card-6 {
    margin-left: -120px;
  }

  .top-card {
    width: 210px;
    margin-left: -20px;
  }
}

.start-game-durak {
  margin: 0 auto;
  background-color: #ffc312;
}
.primary {
  background-color: #ffc312;
  color: #ffc312;
}
.btn-primary {
  background-color: #ffc312;
  color: #000;
}
.form-label {
  color: #ffc312;
}
.game-table {
  height: 1500px;
  background: rgba(34, 139, 34, 0.5);
  margin-left: -20px;
}
.card-grid {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 140px;
}
.player1 {
  grid-area: 1/3;
  margin-top: 10px;
}
.player2 {
  grid-area: 3/4;
}
.player3 {
  grid-area: 3/3;
}
.player4 {
  grid-area: 3/2;
}
.player5 {
  grid-area: 1/2;
  margin-top: 10px;
}
.player6 {
  grid-area: 1/4;
  margin-top: 10px;
}

@media (max-width: 900px) {
  .center-table {
    min-width: 800px;
    height: 480px;
    background: #228b22;
    margin: 30% 40px 30% 40px;
    border: 40px solid rgba(0, 0, 0, 0.9);
    border-radius: 40%;
    position: absolute;
  }
}
@media (min-width: 900px) {
  .center-table {
    min-width: 750px;
    height: 480px;
    background: #228b22;
    /* margin: 30% 40px 30% 40px; */
    border: 40px solid rgba(0, 0, 0, 0.9);
    border-radius: 40%;
    zoom: 120%;
    display: flex;
    position: absolute;
    justify-content: center;
    top: 350px;
    left: 40px;
    right: 40px;
  }
}
@media (max-width: 450px) {
  .game-table {
    width: 109%;
  }
  .center-table {
    min-width: 720px;
    height: 350px;
    background: #228b22;
    margin-left: -140px;
    margin-top: 190px;
    border: 26px solid rgba(0, 0, 0, 0.9);
    border-radius: 40%;
    position: absolute;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  .App-header {
    float: left;
    background: transparent;
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
  }
}

